/* Filters for the Tickets Page */


// Filter block

.mobile-filter-block {
  display: block;

  @media only screen and (min-width: $screen-ipad-v) {
    display: block;
  }

  @media only screen and (min-width: $screen-ipad-h) {
    display: none;
  }

  @media only screen and (min-width: $screen-desktop) {
    display: none;
  }
}

.tickets-filter--row {
  display: none;
  overflow: hidden;

  @media only screen and (min-width: $screen-ipad-v) {
    display: none;
    overflow: hidden;
  }

  @media only screen and (min-width: $screen-ipad-h) {
    display: block;
    margin-bottom: 19px;
  }

  @media only screen and (min-width: $screen-desktop) {
    display: block;
    margin-bottom: 19px;
  }

  .tickets-filter--row--left {
    float: left;
    overflow: hidden;
  }

  .tickets-filter--section{

    float: none;

    @media only screen and (min-width: $screen-ipad-v) {
      float: left;
    }

  }

  .label {
    //font-family: WorkSans;
    color:  #333333;
    font-weight: 700;
    font-size: 11px;
    height: 30px;
    line-height: 36px;
    text-align: left;
    display: block;
    padding-left: 10px;

    @media only screen and (min-width: $screen-ipad-v) {
      color:  #333333;
      font-weight: 700;
      font-size: 11px;
      height: 30px;
      line-height: 36px;
      text-align: left;
      display: block;
      padding-left: 10px;
    }

    //@media only screen and (min-width: $screen-ipad-h) {
    //  font-size: 18px;
    //  height: 44px;
    //  line-height: 39px;
    //  padding-left: 16px;
    //}
    //
    @media only screen and (min-width: $screen-desktop) {
    //
    //  font-size: 18px;
    //  height: 44px;
    //  line-height: 39px;
    //  padding-left: 16px;

      font-size: 14px;
    }

  }

  .tickets-filter--block{

    margin-right: 23px;

    @media only screen and (min-width: $screen-ipad-v) {
      margin-right: 23px;
    }

    //@media only screen and (min-width: $screen-ipad-h) {
    //  margin-right: 37px;
    //}
    //
    //@media only screen and (min-width: $screen-desktop) {
    //  margin-right: 37px;
    //}

    .selectbox {
      min-width: 124px;

      @media only screen and (min-width: $screen-ipad-v) {
        min-width: 124px;
      }

      @media only screen and (min-width: $screen-ipad-h) {
        //min-width: 180px;
      }

      @media only screen and (min-width: $screen-desktop) {
        //min-width: 200px;
        min-width: 150px;
      }

      select {
        color: #333333;
        border-color: #333;
        font-weight: 700;
        font-size: 11px;
        height: 35px;
        padding-left: 10px;
        border-radius: 0;

        @media only screen and (min-width: $screen-ipad-v) {
          color: #333333;
          border-color: #333;
          font-weight: 700;
          font-size: 11px;
          height: 35px;
          padding-left: 10px;
          border-radius: 0;
        }

        //@media only screen and (min-width: $screen-ipad-h) {
        //  font-size: 16px;
        //  padding-left: 16px;
        //  height: 56px;
        //}
        //
        @media only screen and (min-width: $screen-desktop) {
        //  font-size: 18px;
        //  padding-left: 16px;
        //  height: 56px;

          font-size: 13px;
        }
      }
    }

    .selectbox::after {
      display: block;
      content: '';
      position: absolute;
      z-index: 1;
      right: 0px;
      width: 30px;
      height: 35px;
      background-image: url('../img/tickets/desktop/arrow-select.png');
      -webkit-background-size: 17px;
      background-size: 17px;
      background-repeat: no-repeat;
      background-position: 7px 14px;
      top: 0px;
      border-left: 1px solid #333;

      @media only screen and (min-width: $screen-ipad-v) {
        display: block;
        content: '';
        position: absolute;
        z-index: 1;
        right: 0px;
        width: 30px;
        height: 35px;
        background-image: url('../img/tickets/desktop/arrow-select.png');
        -webkit-background-size: 17px;
        background-size: 17px;
        background-repeat: no-repeat;
        background-position: 7px 14px;
        top: 0px;
        border-left: 1px solid #333;
      }

      //@media only screen and (min-width: $screen-ipad-h) {
      //  width: 49px;
      //  height: 56px;
      //  background-image: url('../img/tickets/desktop/arrow-select.png');
      //  -webkit-background-size: 26px;
      //  background-size: 26px;
      //  background-repeat: no-repeat;
      //  background-position: 11px 22px;
      //}
      //
      //@media only screen and (min-width: $screen-desktop) {
      //  width: 49px;
      //  height: 56px;
      //  background-image: url('../img/tickets/desktop/arrow-select.png');
      //  -webkit-background-size: 26px;
      //  background-size: 26px;
      //  background-repeat: no-repeat;
      //  background-position: 11px 22px;
      //
      //}
    }
  }

  .tickets-filter--row--right {

    float: none;
    overflow: hidden;

    @media only screen and (min-width: $screen-ipad-v) {
      float: right;
    }

    .tickets-filter--block {
      margin-right: 0;
    }

    .tickets-filter--btn {
      //font-family: WorkSans;
      color: #fff;
      font-weight: 700;
      font-size: 14px;
      line-height: 36px;
      text-align: center;
      background-color: #4dec8c;
      //min-width: 190px;
      height: 35px;
      cursor: pointer;

      @media only screen and (min-width: $screen-ipad-v) {
        color: #fff;
        font-weight: 700;
        font-size: 19px;
        line-height: 36px;
        text-align: center;
        background-color: #4dec8c;
        min-width: 190px;
        height: 35px;
        cursor: pointer;
      }

      //@media only screen and (min-width: $screen-ipad-h) {
      //  font-size: 24px;
      //  line-height: 56px;
      //  min-width: 210px;
      //  height: 56px;
      //}
      //
      @media only screen and (min-width: $screen-desktop) {
      //
      //  font-size: 30px;
      //  line-height: 56px;
      //
      //  min-width: 302px;
      //  height: 56px;

        font-size: 18px;
      }
    }
  }

}

.mobile-filter-block {
  display: block;

  @media only screen and (min-width: $screen-ipad-v) {
    display: block;
  }

  @media only screen and (min-width: $screen-ipad-h) {
    display: none;
  }

  @media only screen and (min-width: $screen-desktop) {
    display: none;
  }

  .tickets-filter--row {
    display: block;
  }
}

