// Review and pay page

.review_and_pay-section {

  padding: 0;

  @media only screen and (min-width: $screen-ipad-v) {
    padding: 0;
    margin-top: 50px;
  }

  @media only screen and (min-width: $screen-ipad-h) {
    background-color: #f5f5f5;
    margin-bottom: 60px;
  }

  @media only screen and (min-width: $screen-desktop) {
    background-color: #f5f5f5;
    margin-bottom: 47px;
    margin-top: 26px;
  }

  section {

    .top-row {

      color: #333333;
      text-align: left;

      background-color: #fad425;
      height: 50px;
      padding-left: 50px;
      text-transform: uppercase;

      @media only screen and (min-width: $screen-xs) {
        color: #333333;
        text-align: left;

        background-color: #fad425;
        height: 60px;
        padding-left: 84px;
        text-transform: uppercase;
      }

      @media only screen and (min-width: $screen-ipad-v) {
        color: #333333;
        text-align: left;

        background-color: #fad425;
        height: 78px;
        padding-left: 84px;
        text-transform: uppercase;
      }

      @media only screen and (min-width: $screen-ipad-h) {
        height: 125px;
        padding-left: 181px;
      }

      @media only screen and (min-width: $screen-desktop) {
        height: 94px;
        padding-left: 134px;
      }

      h1 {
        font-size: 16px;
        font-weight: 900;
        position: relative;
        line-height: 50px;

        @media only screen and (min-width: $screen-xs) {
          font-size: 22px;
          font-weight: 900;
          position: relative;
          line-height: 60px;
        }

        @media only screen and (min-width: $screen-ipad-v) {
          font-size: 37px;
          font-weight: 900;
          position: relative;
          line-height: 80px;
        }

        @media only screen and (min-width: $screen-ipad-h) {
          font-size: 40px;
          line-height: 125px;
        }

        @media only screen and (min-width: $screen-desktop) {
          font-size: 60px;
          line-height: 125px;
        }
      }

      h1:before {
        display: inline-block;
        content: "";
        position: absolute;
        left: 0;
        top: 0;

        @media only screen and (min-width: $screen-xs) {
          display: inline-block;
          content: "";
          position: absolute;
          left: 0;
          top: 0;
        }

        @media only screen and (min-width: $screen-ipad-v) {
          display: inline-block;
          content: "";
          position: absolute;
          left: 0;
          top: 0;
        }
      }

      p {
        color:  #333333;
        font-size: 17px;
        font-weight: 400;
        text-align: left;

        @media only screen and (min-width: $screen-xs) {
          color:  #333333;
          font-size: 17px;
          font-weight: 400;
          text-align: left;
        }

        @media only screen and (min-width: $screen-ipad-v) {
          color:  #333333;
          font-size: 17px;
          font-weight: 400;
          text-align: left;
        }

        @media only screen and (min-width: $screen-ipad-h) {
          color:  #333333;
          font-size: 17px;
          font-weight: 400;
          text-align: left;
        }

        @media only screen and (min-width: $screen-desktop) {
          color:  #333333;
          font-size: 17px;
          font-weight: 400;
          text-align: left;

        }
      }
    }

    &.section--review-and-pay {

      padding-bottom: 13px;

      @media only screen and (min-width: $screen-xs) {
        padding-bottom: 20px;
      }

      @media only screen and (min-width: $screen-ipad-v) {
        padding-bottom: 20px;
      }

      @media only screen and (min-width: $screen-ipad-h) {
        padding-bottom: 7px;
      }

      @media only screen and (min-width: $screen-desktop) {
        padding-bottom: 7px;
      }

      .top-row {

        height: 64px;
        padding-left: 50px;

        @media only screen and (min-width: $screen-xs) {
          height: 60px;
          padding-left: 84px;
        }

        @media only screen and (min-width: $screen-ipad-v) {
          height: 94px;
          padding-left: 136px;
          margin-top: 26px;
        }

        h1{
          line-height: 1;
          padding-top: 10px;

          @media only screen and (min-width: $screen-ipad-v) {
            line-height: 1;
            padding-top: 12px;
          }

          @media only screen and (min-width: $screen-ipad-h) {
            padding-top: 22px;
          }

          @media only screen and (min-width: $screen-desktop) {
            padding-top: 22px;
            font-size: 44px;
            padding-top: 17px;
          }
        }

        p {

          color: #333;
          font-size: 14px;
          font-weight: 400;
          line-height: 1;
          text-align: left;
          margin-bottom: 0;
          letter-spacing: 0;
          text-transform: none;
          padding-right: 40px;

          @media only screen and (min-width: $screen-xs) {
            color: #333;
            font-size: 10.4px;
            font-weight: 400;
            line-height: 1.3;
            text-align: left;
            margin-bottom: 0;
            letter-spacing: 0;
            text-transform: none;
            padding-right: 0;
          }

          @media only screen and (min-width: $screen-ipad-v) {
            color: #333;
            font-size: 10.4px;
            font-weight: 400;
            line-height: 1.3;
            text-align: left;
            margin-bottom: 0;
            letter-spacing: 0;
            padding-left: 18px;
            text-transform: none;
            padding-right: 0;
          }

          @media only screen and (min-width: $screen-ipad-h) {
            line-height: 1.1;
            font-size: 17px;
            padding-left: 0;
          }

          @media only screen and (min-width: $screen-desktop) {
            line-height: 1.1;
            font-size: 14px;
            padding-left: 0;
          }
        }
      }

      h1:before {
        background-image: url('../img/review-and-pay/desktop/ico-review.png');
        background-repeat: no-repeat;
        -webkit-background-size: 30px;
        background-size: 30px;
        width: 36px;
        height: 42px;
        left: -39px;
        top: 12px;

        @media only screen and (min-width: $screen-xs) {
          background-image: url('../img/review-and-pay/desktop/ico-review.png');
          background-repeat: no-repeat;
          -webkit-background-size: 44px;
          background-size: 44px;
          width: 71px;
          height: 42px;
          left: -62px;
          top: 18px;
        }

        @media only screen and (min-width: $screen-ipad-v) {
          background-image: url('../img/review-and-pay/desktop/ico-review.png');
          background-repeat: no-repeat;
          -webkit-background-size: 44px;
          background-size: 44px;
          width: 71px;
          height: 42px;
          left: -62px;
          top: 19px;
        }

        @media only screen and (min-width: $screen-ipad-h) {
          -webkit-background-size: 71px;
          background-size: 71px;
          width: 71px;
          height: 42px;
          left: -103px;
          top: 33px;
        }

        @media only screen and (min-width: $screen-desktop) {
          -webkit-background-size: 54px;
          background-size: 54px;
          width: 71px;
          height: 42px;
          left: -79px;
          top: 25px;
        }
      }

      .total-row {
        overflow: hidden;

        margin-right: 0;

        @media only screen and (min-width: $screen-ipad-v) {
          overflow: hidden;

          margin-right: 0;
        }

        @media only screen and (min-width: $screen-ipad-h) {
          margin-right: 269px;
        }

        @media only screen and (min-width: $screen-desktop) {
          margin-right: 269px;
        }

        h3 {
