          text-transform: uppercase;
          color: #333;
          font-size: 17px;
          font-weight: 900;
          line-height: 1;
          text-align: left;
          padding-top: 12px;
          padding-bottom: 0;

          @media only screen and (min-width: $screen-xs) {
