          @media only screen and (min-width: $screen-ipad-h) {
            color: #333333;
            //font-size: 24px;
            font-size: 18.5px;
            font-weight: 900;
            line-height: 36px;
            text-align: left;
          }

          @media only screen and (min-width: $screen-desktop) {
            color: #333333;
            font-size: 24px;
            font-weight: 900;
            line-height: 36px;
            text-align: left;
          }
        }

        h3.right {
          float: none;
          font-size: 15px;
          color: #fad425;
          padding-top: 2px;
          padding-right: 120px;
          line-height: 1.2;

          @media only screen and (min-width: $screen-xs) {
            float: right;

            text-transform: uppercase;
            color: #333;
            font-size: 17px;
            font-weight: 900;
            line-height: 1;
            text-align: left;
            padding-top: 12px;
            padding-bottom: 0;
          }

          @media only screen and (min-width: $screen-ipad-v) {
            float: right;
          }
        }
