        @media only screen and (min-width: $screen-desktop) {
          float: right;
          width: 150px;
          margin-left: -150px;
          margin-top: -53px;
          margin-right: 5px;
        }

        .btn--edit {
          text-transform: uppercase;
          border-radius: 4px;
          background-color: #4dec8c;
          width: 100%;
          height: 30px;
          position: relative;
          color: #fffffe;
          font-size: 15px;
          font-weight: 700;
          line-height: 29px;
          text-align: left;
          display: block;
          margin-top: 11px;
          text-decoration: none;
          padding-left: 22px;

          @media only screen and (min-width: $screen-ipad-v) {
            text-transform: uppercase;
            border-radius: 4px;
            background-color: #4dec8c;
            width: 100%;
            height: 30px;
            position: relative;
            color: #fffffe;
            font-size: 15px;
            font-weight: 700;
            line-height: 29px;
            text-align: left;
            display: block;
            margin-top: 11px;
            text-decoration: none;
            padding-left: 22px;
          }

          @media only screen and (min-width: $screen-ipad-h) {
            width: 148px;
            height: 47px;
            font-size: 24px;
            font-weight: 900;
            line-height: 48px;
            text-align: left;
            padding-left: 16px;
          }

          @media only screen and (min-width: $screen-desktop) {
            width: 148px;
            height: 47px;
            font-size: 24px;
            font-weight: 900;
            line-height: 48px;
            text-align: left;
            padding-left: 16px;

          }
        }
        .btn--edit:after {
          display: block;
          content: "";
          position: absolute;
          right: 0;
          top: 0;
          width: 30px;
          height: 28px;
          margin-top: 1px;
          background-image: url('../img/review-and-pay/desktop/ico-edit.png');
          background-repeat: no-repeat;
          -webkit-background-size: 17px;
          background-size: 17px;
          background-position: 47% 38%;
          border-left: 1px solid #fff;

          @media only screen and (min-width: $screen-ipad-v) {
            display: block;
            content: "";
            position: absolute;
            right: 0;
            top: 0;
            width: 30px;
            height: 28px;
            margin-top: 1px;
            background-image: url('../img/review-and-pay/desktop/ico-edit.png');
            background-repeat: no-repeat;
            -webkit-background-size: 17px;
            background-size: 17px;
            background-position: 47% 38%;
            border-left: 1px solid #fff;
          }

          @media only screen and (min-width: $screen-ipad-h) {
            background-image: url('../img/review-and-pay/desktop/ico-edit.png');
            background-repeat: no-repeat;
            -webkit-background-size: 29px;
            background-size: 29px;
            width: 59px;
            height: 44px;
            background-position: 48% 26%;
          }

          @media only screen and (min-width: $screen-desktop) {
            background-image: url('../img/review-and-pay/desktop/ico-edit.png');
            background-repeat: no-repeat;
            -webkit-background-size: 29px;
            background-size: 29px;
            width: 59px;
            height: 44px;
            background-position: 48% 26%;
          }

        }
      }

      h4:first-of-type {
        margin-top: 6px;

        @media only screen and (min-width: $screen-xs) {
          margin-top: 0;
        }
      }

      h4 {
        text-transform: none;
        color: #333;
        font-size: 15.9px;
        font-weight: 700;
        text-align: left;
        padding-top: 0px;

        @media only screen and (min-width: $screen-xs) {
          text-transform: none;
          color: #333;
          font-size: 10.7px;
          font-weight: 700;
          text-align: left;
          padding-top: 0px;
        }

        @media only screen and (min-width: $screen-ipad-v) {
