          }
          &:last-of-type {
            margin-bottom: 10px;
          }
        }
      }

      p {
        color: #333;
        font-size: 15.5px;
        font-weight: 400;
        line-height: 1.2;
        text-align: left;
        margin-bottom: 0;
        letter-spacing: 0.2px;

        @media only screen and (min-width: $screen-xs) {
