// Blog Page

.blog--page {

  .title-block {
    height: 60px;

    @media only screen and (min-width: $screen-ipad-v) {
      height: 125px;
    }
    @media only screen and (min-width: $screen-ipad-h) {
      height: 125px;
    }
    @media only screen and (min-width: $screen-desktop) {
      height: 93px;
    }

    h1 {
      text-align: left;
      //font-family: WorkSans;
      font-family: $ff1;
      color: #333333;
      line-height: 60px;
      font-size: 22px;
      padding-left: 29px;
      font-weight: 900;
      text-transform: uppercase;

      @media only screen and (min-width: $screen-ipad-v) {
        line-height: 116px;
        padding-left: 29px;
        font-size: 37px;
        //line-height: 63px;
      }
      @media only screen and (min-width: $screen-ipad-h) {
        line-height: 116px;
        font-size: 40px;
        padding-left: 29px;
      }
      @media only screen and (min-width: $screen-desktop) {
        line-height: 84px;
        font-size: 45px;
        padding-left: 22px;
      }
    }
  }

  .txt-left{
    text-align: left;
    ul {
      @media only screen and (max-width: 480px) {
        padding-left: 20px;
      }
    }

    ol {
      @media only screen and (max-width: 480px) {
        padding-left: 20px;
      }
    }
  }

  .title-blog {
    font-family: $ff3;
    color: #f0c11c;
    font-weight: 900;
    font-size: 18px;
    line-height: 20px;
  }

  .title-blog-big {
    font-family: $ff3;
    color: #f0c11c;
    text-align: left;
    line-height: 60px;
    font-size: 32px;
    font-weight: 900;
  }

  .title-blog-big-mobile {
    font-family: $ff3;
    color: #f0c11c;
    text-align: left;
    padding-left: 8px;
    line-height: 60px;
    font-size: 32px;
    font-weight: 900;
  }

  .image-title-blog{
    padding-top: 15px;
  }

  .image-title-blog img {
    display:block;
    margin:auto;
  }

  .link-none {
    text-decoration: none;
    cursor: pointer
  }

  .link-blog {
    font-family: $ff1;
    color: #333333;
    background-color: #f0c11c;
    border-radius: 5px;
    line-height: 60px;
    padding: 10px 10px 10px 10px;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
  }

  .pagination{
    margin:20px 0;
    .centered{
      text-align:center;
    }
    .page {
      display: inline-block;
      padding: 4px 10px;
      margin-right: 4px;
      border-radius: 3px;
      border: none;
      font-size: 14px;
      text-decoration: none;
      background: #e1e1e1;
      color: #000000;
      font-weight: bold;
    }

    .page.active {
      border: solid 1px #c0c0c0;
      background: #FFFFFF;
      color: #333;
      text-shadow: 0px 1px 0px rgba(255,255,255, 1);
    }

  }

}